#gameDiv {
    display: flex;
    justify-content: center;
    flex-direction: column;
    grid-column: 1 / 6;
    grid-row: 1 / 4;
    min-height: 363px;
}

ul {
    display: flex;
    align-items: flex-end;
    min-height: 90px;
    align-self: center;
    list-style: none;
    height: 5rem;
}

.letterAndLineContainer{
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
}

.letterAboveLine, .imgAboveLine {
    font-size: 40px;
    color: #fff88bff;
}

.lineForWordToGuess {
    min-width: 20px;
    font-size: 20px;
    color: #fff88bff;
    margin-top: -20px;
}

#mark{
    height: 25px;
    width: 25px;
    opacity: 0;
    display: inline-flex;
}

#buttonDiv {
    align-self: center;
    display: flex;
    height: 3rem;
}

button {
    box-sizing: border-box;
    padding: 10px 30px;
    height: 3rem;
    border-radius: 20px;
    border: none;
    background-color:#844923ff;
    cursor: pointer;
    color: #fff88bff;
    font-size: 18px;
    font-weight: bold;
}

button:hover {
    background-color: #fff88bff;
    color: #533e08d7;
}

#btnIWantAWord {
    min-width: 120px;
    min-height: 30px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: 2px solid #fff88bff;
    align-self: flex-end;
}

#backBtn {
    min-width: 120px;
    min-height: 30px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-left: 2px solid #fff88bff;
    align-self: flex-end;
}

#selectedCatAndLevel {
    position: absolute;
    top: 0;
    display: flex;
    align-items: flex-end;
    width: 100%;
    justify-content: center;
    min-height: 680px;
}

div#selectedCatAndLevel > h1 {
    margin: 10px;
    text-align: left;
    font-size: 18px;
}

div#selectedCatAndLevel > h1:first-child {
    position: absolute;
    top: 0;
    left: 0;
}

@media only screen and (max-width: 768px) {
    #gameDiv {
        height: 60vh;
    }

    button {
        padding: 8px 28px;
        border-radius: 18px;
        font-size: 16px;
    }   

    div#selectedCatAndLevel > h1 {
        font-size: 16px;
    }

    .letterAboveLine {
        font-size: 33px;
        margin-top: 10px;
    }
    
    .lineForWordToGuess {
        min-width: 16px;
        font-size: 18px;
        margin-top: -20px;
    }
    
    #btnIWantAWord {
        border-right: 1px solid #fff88bff;
    }
    
    #checkmark{
        width: 22px;
    }
    
    #backBtn {
        border-left: 1px solid #fff88bff;
    }
}

@media only screen and (max-width: 600px) {
    button {
        padding: 6px 26px;
        border-radius: 16px;
        font-size: 11px;
    }   

    div#selectedCatAndLevel > h1 {
        font-size: 11px;
    }

    .letterAboveLine {
        font-size: 25px;
    }
    
    .lineForWordToGuess {
        font-size: 8px;
        margin-top: -10px;
    }
    
    #checkmark{
        width: 19px;
    }
}

@media only screen and (max-height: 800px) and (orientation: portrait){
    .letterAndLineContainer{
        max-height: 70px;
    }
}

@media only screen and (max-height: 800px) and (orientation: landscape) {
    button {
        font-size: 15px;
    }   

    #buttonDiv {
        margin-bottom: -40px;
    }
}

@media only screen and (max-height: 600px) and (orientation: landscape) {
    div#selectedCatAndLevel > h1 {
        margin-bottom: -80px;
    }
}
